@import "../../assets/styles/variables.scss";
.main-slider {
	@media (max-width: 48em) {
		margin: 0 -15px;
	}
	.swiper-wrapper {
		padding: 10px 0;
	}
	&__slide {
		position: relative;
		max-width: 346px;
		width: 100%;
		max-height: 436px;
		height: 100%;
		@media (max-width: 35em) {
			max-width: 300px;
			max-height: unset;
			height: unset;
		}
		@media (max-width: 30em) {
			max-width: 250px;
		}
	}
	&__link {
		display: block;
		&:focus-visible {
			outline: 2px solid $blue1Color;
			border-radius: 6px;
		}
	}
	&__image {
		display: block;
		width: 100%;
		img {
			max-width: 100%;
		}
	}
	.swiper-button-prev,
	.swiper-button-next {
		width: 60px;
		height: 60px;
		border-radius: 50%;
		background-color: $accentColor;
		transition: background-color 0.3s ease-in-out;
		&::after {
			content: "";
			background-image: url(../../assets/images/icons/arrow.svg);
			width: 24px;
			height: 24px;
		}
		@media (any-hover: hover) {
			&:hover {
				background-color: $accentHoverColor;
			}
		}
		@media (max-width: 48em) {
			display: none;
		}
	}
	.swiper-button-prev {
	}
	.swiper-button-next {
		&::after {
			transform: rotate(180deg);
		}
	}
}
