@import "../../assets/styles/variables.scss";
.first-screen {
	text-align: center;
	background-image: url(../../assets/images/main-slider/bg.jpg);
	background-position: bottom center;
	background-size: auto;
	background-repeat: no-repeat;
	padding: 0 0 135px;
	min-height: 90vh;
	transition: margin 0.3s ease-in-out;
	@media (max-width: 62em) {
		min-height: unset;
		padding-top: 20px;
		padding-bottom: 90px;
	}
	@media (max-width: 48em) {
		padding-bottom: 70px;
	}
	@media (max-width: 30em) {
		background-size: cover;
	}

	// .first-screen__inner
	&__inner {
	}
	// .first-screen__slider
	&__slider {
		margin-bottom: 2em;
		@media (max-width: 62em) {
			margin-bottom: 1em;
		}
		@media (max-width: 35em) {
			margin-bottom: 0.5em;
		}
	}
	// .first-screen__title
	&__title {
		font-size: 3.5rem;
		font-weight: 400;
		line-height: 112%;
		color: $mainDarkColor;
		&:not(:last-child) {
			margin-bottom: 0.15em;
		}
		@media (max-width: 62em) {
			font-size: 2.5rem;
		}
		@media (max-width: 37.5em) {
			font-size: 2rem;
		}
	}
	// .first-screen__description
	&__description {
		margin: 0 auto;
		max-width: 26.5em;
		font-size: 2rem;
		line-height: 125%;
		color: $mainDarkColor;
		&:not(:last-child) {
			margin-bottom: 1.25em;
		}
		@media (max-width: 62em) {
			font-size: 1.5rem;
		}
		@media (max-width: 37.5em) {
			font-size: 1.25rem;
		}
	}
	// .first-screen__button
	&__button {
	}
}
